html{
   background: url(../img/1-Home.png);
   background-repeat: no-repeat;
}

body{
   padding: 0;
   margin: 0;
   font-family: 'Roboto', sans-serif;
   font-size: 13pt;
}

a{
   text-decoration: none;
}

ul{
   list-style: none;
}

.container{

}

header{
  background: url(../img/photo1.png); 
  background-position-y: bottom;
  padding-bottom: 800px;
}

.header__auth{
   background-color: #333;
   font-family: 'Montserrat', sans-serif;
   display: flex;
   justify-content: flex-end;
   padding-right: 300px;
   margin: 0;
   li{
      position: relative;
      padding: 12px 15px;
      :hover{
         padding: 12px 10px;
         background-color: #00c8c8;
         color:#fff;
         transition: all .2s;
      }  
   }
   a{
      color: #858585;
      padding: 0 10px;
   } 
   
}

nav{
   h1 {
      position: absolute;
      font-size: 24pt;
      margin-left: 100px;
      a{
         color: #333;
      }
      }
   ul {
      display: flex;
      font-family: 'Montserrat', sans-serif;
      justify-content: flex-end;
      
      li{
         padding: 20px;
         cursor: pointer;
         -webkit-transition: all 0.2s ease;
         transition: all 0.2s ease;
      }
      table{
         font-family: 'Roboto', sans-serif;
         margin-top: 10px;
         position: absolute;
         box-shadow: 0 0 10px rgba(0,0,0,0.5);
         font-size: 12pt;
         padding: 25px;
         display: none;
         background-color: #fff;     
         th{
            padding: 10px 110px 20px 0;
            color: #575153;
         }
         td{
            padding: 4px;
         }
         a{
            color: #858585;
         }
      }
      input{
         height: 35px;
         width: 200px;
         margin: 20px 20px 0 80px;
      }
      :hover {
         color: #00c8c8;
      }
      :hover table{
         display: block;
      }
   }
}